import {
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Subscription } from 'rxjs';
import { DelayService, FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType } from 'shared-code-lib';
import { CustomProperty } from '../../InterFAX/Models/admin';
import { Filter } from '../../models/filter';
import { InterFAXService } from '../../services/interfax.service';
import { FilterByInterfaxInboxPropertiesComponentLoadedService } from '../../services/FilterByInterfaxInboxPropertiesComponentLoaded.service';
import { FilterBaseComponent } from '../filter-base.component';

@Component({
  selector: 'app-filter-by-interfax-inbox-properties',
  templateUrl: './filter-by-interfax-inbox-properties.component.html',
  styleUrls: ['./filter-by-interfax-inbox-properties.component.scss'],
})
export class FilterByInterfaxInboxPropertiesComponent extends FilterBaseComponent implements OnInit, OnDestroy {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  property = '';
  contains = '';

  fieldProperty: FieldDefinition;
  fieldContains: FieldDefinition;

  timer = null;
  lastFilterValue: string;

  subscriptions = new Subscription();

  propertyDataSource = new FixedDataSource(new Array<IFieldOptions>());

  constructor(
    private interfaxService: InterFAXService,
    private translateService: TranslateService,
    private cdk: ChangeDetectorRef,
    private elementRef: ElementRef,
    private delay: DelayService,
    private componentLoadedService: FilterByInterfaxInboxPropertiesComponentLoadedService
  ) {
    super();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  ngOnInit(): void {
    this.validateProperties();
  }

  clearFilter = (): void => this.setValue(':');

  setValue = (val: string): void => {
    const parts = this.getFilterParts(val);
    if (parts.length < 2) return;

    this.property = parts[0];
    this.contains = parts[1];
    this.cdk.detectChanges();

    this.setFilterValue();
  };

  onPropertyChanged = (property: string): void => {
    let previousProperty = this.property;
    this.property = property;
    if (previousProperty) {
      this.onFieldChanged();
    } else {
      this.delay.execute(() => {
        this.elementRef.nativeElement.querySelector('.filter-by-interfax-inbox-properties .field-text').focus();
      }, 100);
    }
  };

  onContainsChanged = (contains: string): void => {
    this.contains = contains;
    this.onFieldChanged();
  };

  onFieldChanged = (): void => {
    this.setFilterValue();

    if (this.timer) clearTimeout(this.timer);
    this.timer = setTimeout(this.fireFieldChanged, 250);
  };

  fireFieldChanged = (): void => {
    if (this.lastFilterValue !== this.filter.value) {
      this.selected.emit(this.filter.value);
      this.lastFilterValue = this.filter.value;
    }
  };

  setFilterValue = (): void => {
    this.filter.value = this.property ? `${this.property}:${this.contains}` : '';
  };

  optionsLoaded = (items: IFieldOptions[], interFAXPropertiesDataSource: InterFAXPropertiesDataSource): void => {
    const selectedOption = items.find(o => o.value !== '' && this.filter.value.startsWith(o.value));
    if (selectedOption === undefined) this.filter.value = '';

    this.createFields(interFAXPropertiesDataSource);
    this.initializeFilter();
    this.subscriptions.add(this.filter.clearFilter.subscribe(this.clearFilter));
    this.componentLoadedService.notifyComponentLoaded();
  }

  validateProperties = (): void => {
    const interFAXPropertiesDataSource = new InterFAXPropertiesDataSource(this.interfaxService, this.translateService);
    interFAXPropertiesDataSource.getItems(null, null, (cps) => this.optionsLoaded(cps, interFAXPropertiesDataSource));
  }

  createFields = (interFAXPropertiesDataSource: InterFAXPropertiesDataSource): void => {
    this.fieldProperty = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      smallLabel: this.translateService.instant('filter.property'),
      placeholder: '',
      required: false,
      datasource: interFAXPropertiesDataSource
    });

    this.fieldContains = new FieldDefinition({
      type: MetaDataType.Text,
      label: '',
      searchable: true,
      placeholder: '',
      required: false,
    });
  };
}

class InterFAXPropertiesDataSource extends FixedDataSource {
  constructor(private interfaxService: InterFAXService, private translateService: TranslateService) {
    super();
  }

  optionsLoaded = false;

  successCallBack: (items: IFieldOptions[]) => void;
  failureCallBack: (err: any) => void;

  getItems(
    startsWith: string = null,
    currentValue: string = null,
    successCallBack: (items: IFieldOptions[]) => void = null,
    failureCallBack: (err: any) => void = null
  ): void {
    if (this.optionsLoaded) {
      successCallBack(this.options);
      return;
    }

    this.successCallBack = successCallBack;
    this.failureCallBack = failureCallBack;

    this.interfaxService.getCustomProperties((cps) => this.propertiesLoaded(cps, successCallBack), this.failed);
  }

  propertiesLoaded = (properties: Array<CustomProperty>, successCallBack?: (items: IFieldOptions[]) => void): void => {
    const options = new Array<IFieldOptions>();

    properties.forEach((p) => options.push({ name: p.name, value: `#${encodeURIComponent(p.name)}#${p.type}` }));

    options.push({
      name: this.translateService.instant(`filter-by-interfax-outbox-properties.select-property`),
      value: '',
    });

    const faxProperties = [
      '@tag',
      '@reference',
      '@documentid',
      '@transactionid',
      '@csid',
      '@ani',
      '@name',
      '@messageid',
    ];
    for (const property of faxProperties) {
      options.push({
        name: this.translateService.instant(`filter-by-interfax-inbox-properties.${property}`),
        value: property,
      });
    }

    options.sort((x, y) => x.name.toLowerCase().localeCompare(y.name.toLowerCase()));

    this.options = options;
    this.optionsLoaded = true;

    this.successCallBack(options);
    if (successCallBack) successCallBack(options);
  };

  failed = (err): void => {
    this.failureCallBack(err);
  };
}
