import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import {
  DelayService,
  FieldDefinition,
  FixedDataSource,
  IFieldOptions,
  MetaDataType,
  RolePermissions,
} from 'shared-code-lib';
import { Log } from '../../core/log';
import { Filter } from '../../models/filter';
import { HydraApiService } from '../../services/hydra-api.service';
import { User } from '../../services/models';
import { UserStateService } from '../../services/user-state.service';
import { FilterBaseComponent } from '../filter-base.component';

@Component({
  selector: 'app-filter-by-owner',
  templateUrl: './filter-by-owner.component.html',
  styleUrls: ['./filter-by-owner.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FilterByOwnerComponent extends FilterBaseComponent implements OnInit, OnDestroy {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  value = '';
  fieldUsers: FieldDefinition;
  usersDataSource: FixedDataSource;

  subscriptions = new Subscription();

  constructor(
    private translate: TranslateService,
    private hydraApi: HydraApiService,
    private delay: DelayService,
    private route: ActivatedRoute,
    private toast: ToastrService,
    private userState: UserStateService
  ) {
    super();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  ngOnInit() {
    this.usersDataSource = new FixedDataSource(new Array<IFieldOptions>());

    this.fieldUsers = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      placeholder: '',
      required: false,
      datasource: this.usersDataSource,
    });

    this.subscriptions.add(
      this.route.params.subscribe((params: Params) => {
        if (this.userState.hasRolePermission(RolePermissions.BAMBA_ADMIN) && params.folder.indexOf('@') != -1) {
          this.hydraApi.getUsersByTenantId(params.folder.split('@')[1], this.usersLoaded, this.usersLoadFailed);
        } else {
          this.hydraApi.getUsers(this.usersLoaded, this.usersLoadFailed);
        }
      })
    );

    this.initializeFilter();
    this.subscriptions.add(this.filter.clearFilter.subscribe(this.clearFilter));
  }

  clearFilter = (): void => {
    this.setValue('');
  };

  setValue = (val: string): void => {
    this.filter.value = this.value = val;
  };

  usersLoaded = (users: Array<User>): void => {
    const items: Array<IFieldOptions> = users.map((u) => {
      return { name: u.name, value: u.email };
    });

    items.sort((x, y) => x.name.toLowerCase().localeCompare(y.name.toLowerCase()));
    items.unshift({ name: this.translate.instant('filter.filter-by-user-none'), value: '' });

    this.usersDataSource.updateOptions(items);
    this.delay.execute(() => {
      this.value = this.filter.value;
    }, 5);
  };

  usersLoadFailed = (err: any): void => {
    Log.error('FilterByOwnerComponent.usersLoadFailed', err);
    this.toast.error(this.translate.instant('filter.users-load-failed'));
  };

  onFieldChanged = (value: string): void => {
    this.filter.value = value;
    this.selected.emit(value);
  };
}
