import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Subscription } from 'rxjs';
import { FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType } from 'shared-code-lib';
import { Filter } from '../../models/filter';
import { FilterBaseComponent } from '../filter-base.component';

@Component({
  selector: 'app-filter-by-interfax-outbox-properties',
  templateUrl: './filter-by-interfax-outbox-properties.component.html',
  styleUrls: ['./filter-by-interfax-outbox-properties.component.scss'],
})
export class FilterByInterfaxOutboxPropertiesComponent extends FilterBaseComponent implements OnInit, OnDestroy {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  property = '';
  contains = '';

  lastFilterValue: string;

  fieldProperty: FieldDefinition;
  fieldContains: FieldDefinition;

  timer = null;

  propertyDataSource = new FixedDataSource(new Array<IFieldOptions>());

  subscriptions = new Subscription();

  constructor(private translateService: TranslateService) {
    super();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  clearFilter = (): void => this.setValue(':');

  setValue = (val: string): void => {
    const parts = this.getFilterParts(val);
    if (parts.length < 2) return;

    this.property = parts[0];
    this.contains = parts[1];

    this.setFilterValue();
  };

  ngOnInit(): void {
    this.createFields();
    this.createPropertyDataSource();

    this.initializeFilter();
    this.subscriptions.add(this.filter.clearFilter.subscribe(this.clearFilter));
  }

  createPropertyDataSource = (): void => {
    const properties = ['subject', 'faxnumber', 'notification', 'transactionid', 'contactName'];

    this.propertyDataSource.options.push({
      name: this.translateService.instant(`filter-by-interfax-outbox-properties.select-property`),
      value: '',
    });

    for (const property of properties) {
      this.propertyDataSource.options.push({
        name: this.translateService.instant(`filter-by-interfax-outbox-properties.${property}`),
        value: property,
      });
    }

    this.propertyDataSource.options.sort((x, y) => x.name.toLowerCase().localeCompare(y.name.toLowerCase()));
  };

  onPropertyChanged = (property: string): void => {
    this.property = property;
    this.onFieldChanged();
  };

  onContainsChanged = (contains: string): void => {
    this.contains = contains;
    this.onFieldChanged();
  };

  onFieldChanged = (): void => {
    this.setFilterValue();

    if (this.timer) clearTimeout(this.timer);
    this.timer = setTimeout(this.fireFieldChanged, 250);
  };

  fireFieldChanged = (): void => {
    if (this.lastFilterValue !== this.filter.value) {
      this.selected.emit(this.filter.value);
      this.lastFilterValue = this.filter.value;
    }
  };

  setFilterValue = (): void => {
    this.filter.value = this.property ? `${this.property}:${this.contains}` : '';
  };

  createFields = (): void => {
    this.fieldProperty = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      placeholder: '',
      required: false,
      datasource: this.propertyDataSource,
      smallLabel: this.translateService.instant('filter.property'),
    });

    this.fieldContains = new FieldDefinition({
      type: MetaDataType.Text,
      label: '',
      placeholder: '',
      required: false,
      searchable: true,
    });
  };
}
