import { Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';
import { Filter } from '../../models/filter';

@Component({
  selector: 'app-filter-by-user-property',
  templateUrl: './filter-by-user-property.component.html',
  styleUrls: ['./filter-by-user-property.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FilterByUserPropertyComponent {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  onFilterSelected = (value: string) => this.selected.emit(value);
}
