import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import {
  DelayService,
  FieldDefinition,
  FixedDataSource,
  IFieldOptions,
  MetaDataType,
  RolePermissions,
} from 'shared-code-lib';
import { Log } from '../../core/log';
import { Filter } from '../../models/filter';
import { Workflow } from '../../models/workflow';
import { HydraApiService } from '../../services/hydra-api.service';
import { UserStateService } from '../../services/user-state.service';
import { FilterBaseComponent } from '../filter-base.component';

@Component({
  selector: 'app-filter-by-workflow',
  templateUrl: './filter-by-workflow.component.html',
  styleUrls: ['./filter-by-workflow.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FilterByWorkflowComponent extends FilterBaseComponent implements OnInit, OnDestroy {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  fieldWorkflows: FieldDefinition;
  fieldSteps: FieldDefinition;

  workflows: Array<Workflow>;
  workflowId: string = '';
  workflowStepId: string = '';
  workflowsDataSource = new FixedDataSource(new Array<IFieldOptions>());
  workflowStepDataSource = new FixedDataSource(new Array<IFieldOptions>());

  subscriptions = new Subscription();

  constructor(
    private hydraApi: HydraApiService,
    private translate: TranslateService,
    private delay: DelayService,
    private route: ActivatedRoute,
    private toast: ToastrService,
    private userState: UserStateService
  ) {
    super();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  ngOnInit() {
    this.workflowsDataSource.options.unshift({
      name: this.translate.instant('filter.filter-by-workflow-none'),
      value: '',
    });
    this.workflowStepDataSource.options.unshift({
      name: this.translate.instant('filter.filter-by-workflowstep-none'),
      value: '',
    });

    this.createFields();

    this.subscriptions.add(
      this.route.params.subscribe((params: Params) => {
        if (this.userState.hasRolePermission(RolePermissions.BAMBA_ADMIN) && params.folder.indexOf('@') != -1) {
          this.hydraApi.getWorkflowsByTenantId(
            params.folder.split('@')[1],
            this.onWorkflowsLoaded,
            this.onWorkflowsLoadFailed
          );
        } else {
          this.hydraApi.getWorkflows(this.onWorkflowsLoaded, this.onWorkflowsLoadFailed);
        }
      })
    );

    this.initializeFilter();
    this.subscriptions.add(this.filter.clearFilter.subscribe(this.clearFilter));
  }

  clearFilter = (): void => this.setValue(':');

  setValue = (val: string): void => {
    const parts = this.getFilterParts(val);
    if (parts.length < 2) return;

    this.workflowId = parts[0];
    this.workflowStepId = parts[1];

    this.setFilterValue();
  };

  onWorkflowsLoaded = (workflows: Array<Workflow>): void => {
    this.workflows = workflows;

    const items = workflows.map((w) => {
      return { name: w.name, value: w.id };
    });

    items.sort((x, y) => x.name.toLowerCase().localeCompare(y.name.toLowerCase()));
    items.unshift({ name: this.translate.instant('filter.filter-by-workflow-none'), value: '' });

    this.workflowsDataSource.updateOptions(items);

    const parts = this.filter.value.split(':');
    if (parts && parts.length === 2) {
      this.delay.execute(() => {
        this.workflowId = parts[0];
        this.loadWorkflowSteps();
        this.workflowStepId = parts[1];
      }, 5);
    }
  };

  onWorkflowsLoadFailed = (err: any): void => {
    Log.error('FilterByWorkflowCompoent Failed to load workflows', err);
    this.toast.error(this.translate.instant('filter.workflows-load-failed'));
  };

  createFields = (): void => {
    this.fieldWorkflows = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      placeholder: '',
      required: false,
      datasource: this.workflowsDataSource,
    });

    this.fieldSteps = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      placeholder: '',
      required: false,
      datasource: this.workflowStepDataSource,
    });
  };

  onWorkflowChanged = (wid: string): void => {
    this.workflowId = wid;
    this.workflowStepId = '';

    this.onFieldChanged();
    this.loadWorkflowSteps();
  };

  loadWorkflowSteps() {
    const wf = this.workflows.find((wf) => wf.id == this.workflowId);

    const steps: Array<IFieldOptions> = wf
      ? wf.workflowSteps.map((ws) => {
          return { name: ws.name, value: ws.id };
        })
      : [];

    steps.sort((x, y) => x.name.toLowerCase().localeCompare(y.name.toLowerCase()));

    steps.unshift({ name: this.translate.instant('filter.filter-by-workflowstep-none'), value: '' });

    this.workflowStepDataSource.updateOptions(steps);
  }

  onWorkflowStepChanged = (wsid: string): void => {
    this.workflowStepId = wsid;
    this.onFieldChanged();
  };

  onFieldChanged = (): void => {
    this.setFilterValue();
    this.selected.emit(this.filter.value);
  };

  setFilterValue = (): void => {
    this.filter.value = `${this.workflowId}:${this.workflowStepId}`;
  };
}
