import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Subscription } from 'rxjs';
import { FieldDefinition, MetaDataType } from 'shared-code-lib';
import { Filter } from '../../models/filter';
import { FilterBaseComponent } from '../filter-base.component';

@Component({
  selector: 'app-filter-by-fax-user-username',
  templateUrl: './filter-by-fax-user-username.component.html',
  styleUrls: ['./filter-by-fax-user-username.component.scss'],
})
export class FilterByFaxUserUsernameComponent extends FilterBaseComponent implements OnInit, OnDestroy {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  userIdProperty: string = '';

  fieldUsernameProperty: FieldDefinition;

  subscriptions = new Subscription();

  constructor(private translateService: TranslateService) {
    super();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  ngOnInit(): void {
    this.createField();
    this.initializeFilter();
    this.subscriptions.add(this.filter.clearFilter.subscribe(this.clearFilter));
  }

  clearFilter = (): void => this.setValue('');

  setValue = (val: string): void => void (this.userIdProperty = this.filter.value = val);

  onUsernamePropertyChange = (cp: string): void => {
    if (cp == this.filter.value) return;
    this.userIdProperty = this.filter.value = cp;
    this.selected.emit(this.userIdProperty);
  };

  createField = (): void => {
    this.fieldUsernameProperty = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('filter.filter-by-fax-user-username.username'),
      placeholder: this.translateService.instant('filter.filter-by-fax-user-username.username'),
      smallLabel: this.translateService.instant('filter.filter-by-fax-user-username.username'),
      required: false,
      hideLabel: true,
    });
  };
}
