import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Subscription } from 'rxjs';
import { FieldDefinition, MetaDataType } from 'shared-code-lib';
import { Filter } from '../../models/filter';
import { FilterBaseComponent } from '../filter-base.component';

@Component({
  selector: 'app-filter-by-faxuser-faxnumber',
  templateUrl: './filter-by-faxuser-faxnumber.component.html',
  styleUrls: ['./filter-by-faxuser-faxnumber.component.scss'],
})
export class FilterByFaxuserFaxnumberComponent extends FilterBaseComponent implements OnInit, OnDestroy {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  userIdProperty: string = '';

  fieldUsernameProperty: FieldDefinition;

  timer = null;
  lastFilterValue: string;

  subscriptions = new Subscription();

  constructor(private translateService: TranslateService) {
    super();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  ngOnInit(): void {
    this.createField();
    this.initializeFilter();
    this.subscriptions.add(this.filter.clearFilter.subscribe(this.clearFilter));
  }

  clearFilter = (): void => {
    this.setValue('');
  };

  onUsernamePropertyChange = (cp: string): void => {
    this.setValue(cp);
    this.onFieldChanged();
  };

  setValue = (value: string) => void (this.userIdProperty = this.filter.value = value);

  onFieldChanged = (): void => {
    if (this.timer) clearTimeout(this.timer);
    this.timer = setTimeout(this.fireFieldChanged, 800);
  };

  fireFieldChanged = (): void => {
    if (this.lastFilterValue !== this.filter.value) {
      this.selected.emit(this.filter.value);
      this.lastFilterValue = this.filter.value;
    }
  };

  createField = (): void => {
    this.fieldUsernameProperty = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('filter.filter-by-fax-user-faxNumber.faxnumber'),
      placeholder: this.translateService.instant('filter.filter-by-fax-user-faxNumber.faxnumber'),
      smallLabel: this.translateService.instant('filter.filter-by-fax-user-faxNumber.faxnumber'),
      required: false,
      hideLabel: true,
    });
  };
}
