import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { IFieldOptions } from 'shared-code-lib';
import { Log } from '../../../../app/blocks/core/log';
import { VersionStatusDataSource } from '../../../views/admin-olc/models/filters/version-status-data-source';
import { Filter } from '../../models/filter';

@Component({
  selector: 'app-filter-by-olc-template-version-status',
  templateUrl: './filter-by-olc-template-version-status.component.html',
  styleUrls: ['./filter-by-olc-template-version-status.component.scss'],
})
export class FilterByOLCTemplateVersionStatusComponent implements OnInit {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();
  options: Array<IFieldOptions> = [];

  constructor(private translate: TranslateService, private toastr: ToastrService) {}

  ngOnInit(): void {
    new VersionStatusDataSource(this.translate).getItems(
      '',
      '',
      this.onVersionStatusesLoaded,
      this.onVersionStatusesLoadFailed
    );
  }

  onVersionStatusesLoaded = (items: IFieldOptions[]) => {
    this.options.push({ name: this.translate.instant(`admin-olc-filters.all`), value: '' });

    if (this.filter.id.includes('Approvals')) {
      items = items.filter((x) => x.value === '1' || x.value === '2' || x.value === '3');
    }

    if (this.filter.id.includes('Production')) {
      items = items.filter((x) => x.value === '4' || x.value === '5' || x.value === '6');
    }

    items.forEach((x) => this.options.push({ name: x.name, value: x.value }));
  };

  onVersionStatusesLoadFailed = (err) => {
    this.toastr.error(this.translate.instant('admin-olc-templates-filters.statuses-load-failed'));
    Log.error('FilterByOLCTemplateVersionStatusComponent: Failed to load version statuses', err);
  };

  onFilterSelected = (value: string) => this.selected.emit(value);
}
