import {
  Component,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  SimpleChanges,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';

import { PdfViewerComponent } from 'ng2-pdf-viewer';
import { Subject } from 'rxjs';
import { Annotation } from 'shared-code-lib';
import { PageState } from '../../../../views/document-capture/capture-models';
import { PreviewPage } from '../document-preview.component';

@Component({
  selector: 'app-preview-thumbnails',
  templateUrl: './preview-thumbnails.component.html',
  styleUrls: ['./preview-thumbnails.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class PreviewThumbnailsComponent implements OnChanges {
  @ViewChild('thumbnailsPanelScrollbar', { static: true })
  thumbnailsPanelScrollbar: PerfectScrollbarDirective;
  @ViewChild('viewer', { static: false })
  viewer: PdfViewerComponent;
  @Output() setCurrentPage = new EventEmitter<PreviewPage>();
  @Input() pages: Array<PreviewPage>;
  @Input() annotationsAllowed: boolean = false;

  currentPage: PreviewPage;
  waitingPages: Array<PreviewPage> = [];
  gettingThumbnailInProgress: boolean;
  widthResizedSubject: Subject<void>;
  currentPageNumber = 1;

  constructor() {
    this.widthResizedSubject = new Subject<void>();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.pages) {
      this.waitingPages = [];

      if (!this.currentPage) {
        this.thumbnailsPanelScrollbar.scrollToTop(0, 0);
      }
    }
  }

  getThumbnail(page: PreviewPage) {
    if (this.gettingThumbnailInProgress) {
      page.state = PageState.Loading;
      this.waitingPages.push(page);
    } else {
      page.state = PageState.Loading;
      this.gettingThumbnailInProgress = true;
      page
        .getPage()
        .then(() => (this.currentPage = page))
        .catch((err) => {
          this.deletePageFromWaitingList(page);
          page.state = PageState.FailedLoad;
          this.gettingThumbnailInProgress = false;

          if (this.waitingPages.length) {
            this.getThumbnail(this.waitingPages[0]);
          }
        });
    }
  }

  onRender(): void {
    if (!this.currentPage) {
      return;
    }

    const canvas = document.querySelector('.canvasWrapper canvas') as any;

    this.currentPage.thumbnailUrl = canvas.toDataURL('image/jpeg');
    this.currentPage.state = PageState.Loaded;
    if (this.currentPage.pageNumber === this.currentPageNumber) {
      this.setCurrentPage.emit(this.currentPage);
    }

    this.gettingThumbnailInProgress = false;

    this.deletePageFromWaitingList(this.currentPage);
    this.currentPage = null;
    if (this.waitingPages.length) {
      this.getThumbnail(this.waitingPages[0]);
    }
    this.thumbnailsPanelScrollbar?.update();
  }

  deletePageFromWaitingList(page: PreviewPage): void {
    const pageIndex = this.waitingPages.findIndex((p) => p.id === page.id);
    if (pageIndex > -1) {
      this.waitingPages.splice(pageIndex, 1);
    }
  }

  widthResized = (): void => {
    this.widthResizedSubject.next();

    if (!this.viewer) return;

    this.viewer.updateSize();
  };

  selectPage(page: PreviewPage): void {
    this.setCurrentPage.emit(page);
  }

  onAnnotationsLoaded = () => {
    if (!this.annotationsAllowed) return;

    this.pages.forEach((page) => {
      page.onAnnotationsChanged();
    });
  };

  onAnnotationAdded = (annotation: Annotation) => {
    if (!this.annotationsAllowed) return;

    const page = this.pages.find((x) => x.pageNumber == annotation.pageNumber);
    if (!page) return;

    if (!page.annotations.find((x) => x.id == annotation.id)) page.annotations.push(annotation);
    page.onAnnotationsChanged();
  };

  onAnnotationUpdated = (annotation: Annotation) => {
    if (!this.annotationsAllowed) return;

    const page = this.pages.find((x) => x.pageNumber == annotation.pageNumber);
    if (!page) return;

    const index = page.annotations.findIndex((x) => x.id == annotation.id);
    if (index == -1) return;

    page.annotations[index] = annotation;
    page.onAnnotationsChanged();
  };

  onAnnotationRemoved = (annotation: Annotation) => {
    if (!this.annotationsAllowed) return;

    const page = this.pages.find((x) => x.pageNumber == annotation.pageNumber);
    if (!page) return;

    const index = page.annotations.findIndex((x) => x.id == annotation.id);

    if (index != -1) page.annotations.splice(index, 1);
    page.onAnnotationsChanged();
  };
}
