import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Subscription } from 'rxjs';
import { FieldDefinition, MetaDataType } from 'shared-code-lib';
import { Filter } from '../../models/filter';
import { FilterBaseComponent } from '../filter-base.component';

@Component({
  selector: 'filter-by-text-property',
  templateUrl: './filter-by-text-property.component.html',
  styleUrls: ['./filter-by-text-property.component.scss'],
})
export class FilterByTextPropertyComponent extends FilterBaseComponent implements OnInit, OnDestroy {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Input() property: string = '';
  @Input() label: string = '';
  @Input() placeholder: string = '';
  @Output() selected = new EventEmitter<string>();

  fieldName: FieldDefinition;
  subscriptions = new Subscription();

  constructor(private translate: TranslateService) {
    super();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  ngOnInit(): void {
    this.createFields();

    this.initializeFilter();
    this.subscriptions.add(this.filter.clearFilter.subscribe(this.clearFilter));
  }

  clearFilter = (): void => this.setValue('');

  setValue = (val: string): void => {
    this.property = val;
    this.onPropertyChanged(val);
  };

  onPropertyChanged = (property: string): void => {
    this.filter.value = property;
    this.selected.emit(this.filter.value);
  };

  createFields = (): void => {
    this.fieldName = new FieldDefinition({
      type: MetaDataType.Text,
      label: '',
      smallLabel: this.label ? this.translate.instant(this.label) : '',
      placeholder: this.placeholder ? this.translate.instant(this.placeholder) : '',
      required: false,
    });
  };
}
