import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import {
  FieldDefinition,
  FixedDataSource,
  IFieldOptions,
  MetaDataDefinition,
  MetaDataType,
  RolePermissions,
} from 'shared-code-lib';
import { Filter } from '../../models/filter';
import { HydraApiService } from '../../services/hydra-api.service';
import { UserStateService } from '../../services/user-state.service';
import { FilterBaseComponent } from '../filter-base.component';

@Component({
  selector: 'app-filter-by-metadata',
  templateUrl: './filter-by-metadata.component.html',
  styleUrls: ['./filter-by-metadata.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FilterByMetadataComponent extends FilterBaseComponent implements OnInit, OnDestroy {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  metaDataId = '';
  contains = '';

  fieldMetaData: FieldDefinition;
  fieldContains: FieldDefinition;

  timer = null;
  lastFilterValue: string;

  metaDataDataSource = new FixedDataSource(new Array<IFieldOptions>());

  subscriptions = new Subscription();

  constructor(
    private translateService: TranslateService,
    private userStateService: UserStateService,
    private hydraApi: HydraApiService,
    private route: ActivatedRoute,
    private toast: ToastrService
  ) {
    super();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  ngOnInit(): void {
    this.createFields();

    this.subscriptions.add(
      this.route.params.subscribe((params: Params) => {
        if (this.userStateService.hasRolePermission(RolePermissions.BAMBA_ADMIN) && params.folder.indexOf('@') != -1) {
          this.hydraApi.getMetaDataDefinitionsByTenantId(
            params.folder.split('@')[1],
            this.prepareMetaData,
            this.dataFieldsLoadFailed
          );
        } else {
          this.prepareMetaData(this.userStateService.user.metaData);
        }
      })
    );

    this.initializeFilter();
    this.subscriptions.add(this.filter.clearFilter.subscribe(this.clearFilter));
  }

  dataFieldsLoadFailed = (err: any): void => {
    this.toast.error(this.translateService.instant('export-metadata.data-fields-load-failed'));
  };

  prepareMetaData = (metaData: MetaDataDefinition[]): void => {
    metaData
      .filter((md) => md.type !== MetaDataType.LineItems)
      .forEach((md) => {
        this.metaDataDataSource.options.push({
          name: md.label.trim(),
          value: md.name.trim(),
        });
      });

    this.metaDataDataSource.options.unshift({
      name: this.translateService.instant('filter.filter-by-metadata-none'),
      value: '',
    });

    this.addWorkItemIdToSearch();

    this.metaDataDataSource.options.sort((x, y) => x.name.toLowerCase().localeCompare(y.name.toLowerCase()));
    this.metaDataDataSource.updateOptions(this.metaDataDataSource.options);

    this.metaDataId = '';
  };

  clearFilter = (): void => this.setValue(':');

  setValue = (val: string): void => {
    const parts = this.getFilterParts(val);
    if (parts.length < 2) return;

    this.metaDataId = parts[0];
    this.contains = parts[1];

    this.setFilterValue();
  };

  addWorkItemIdToSearch = (): void => {
    this.metaDataDataSource.options.push({
      name: this.translateService.instant('filter.filter-by-workitemid'),
      value: '_id',
    });
  };

  onMetaDataChanged = (mid: string): void => {
    this.metaDataId = mid;

    this.onFieldChanged();
  };

  onContainsChanged = (contains: string): void => {
    this.contains = contains;

    this.onFieldChanged();
  };

  onFieldChanged = (): void => {
    this.setFilterValue();

    if (this.timer) clearTimeout(this.timer);
    this.timer = setTimeout(this.fireFieldChanged, 250);
  };

  fireFieldChanged = (): void => {
    if (this.lastFilterValue !== this.filter.value) {
      this.selected.emit(this.filter.value);
      this.lastFilterValue = this.filter.value;
    }
  };

  setFilterValue = (): void => {
    this.filter.value = this.metaDataId ? `${this.metaDataId}:${this.contains}` : '';
  };

  createFields = (): void => {
    this.fieldMetaData = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      placeholder: '',
      required: false,
      datasource: this.metaDataDataSource,
    });

    this.fieldContains = new FieldDefinition({
      type: MetaDataType.Text,
      label: '',
      placeholder: '',
      required: false,
    });
  };
}
