import {
  Component,
  ElementRef,
  EventEmitter,
  HostListener,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';

import { TranslateService } from '@ngx-translate/core';
import { PdfViewerComponent } from 'ng2-pdf-viewer';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { ToastrService } from 'ngx-toastr';
import { Subject, Subscription } from 'rxjs';

import {
  Annotation,
  AnnotationTypes,
  DelayService,
  FieldDefinition,
  InboundLogBarcode,
  ItemTableDataSource,
  LoginModes,
  MetaDataType,
  RolePermissions,
  SessionStorageService,
  TableItem,
  UserPreferencesService,
} from 'shared-code-lib';
import { SharePermissions } from '../../../blocks/InterFAX/Models/inbound';
import { Action, ActionEvent } from '../../../blocks/core/actions';
import { Filename } from '../../../blocks/core/fileName';
import { ActionService } from '../../../blocks/services/action.service';
import { ApiService } from '../../../blocks/services/api.service';
import { ErrorsService } from '../../../blocks/services/errors.service';
import { InterFAXService } from '../../../blocks/services/interfax.service';
import { ApiResult } from '../../../blocks/services/models';
import { SpinnerStateService } from '../../../blocks/services/spinner-state.service';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { FitToMeasurement } from '../../../blocks/shared/enumes';
import { PageState, Preference } from '../../document-capture/capture-models';
import { AnnotationsComponent } from './annotations/annotations.component';
import { PreviewThumbnailsComponent } from './preview-thumbnails/preview-thumbnails.component';

@Component({
  selector: 'app-document-preview',
  templateUrl: './document-preview.component.html',
  styleUrls: ['./document-preview.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class DocumentPreviewComponent implements OnInit, OnChanges, OnDestroy {
  @ViewChild('viewer', { static: false }) viewer: PdfViewerComponent;
  @ViewChild('thumbnailsPanel', { static: true }) thumbnailsPanelElementRef: ElementRef;
  @ViewChild('thumbnails', { static: false }) thumbnails: PreviewThumbnailsComponent;
  @ViewChild(PerfectScrollbarDirective) directiveRef?: PerfectScrollbarDirective;
  @ViewChild(AnnotationsComponent, { static: false }) annotations: AnnotationsComponent;

  @Input() selectedItems: Array<any>;
  @Input() folderId: string;
  @Input() documentNameEditingAllowed = false;
  @Input() previewDocument: PreviewDocument;

  @Output() documentReloadRequired = new EventEmitter();

  currentPage: PreviewPage;
  waitingPages: Array<PreviewPage> = [];
  gettingThumbnailInProgress: boolean;
  thumbnailsWidthPreferenceKey: string;
  thumbnailsWidth: string;
  isLoading = true;
  showMessage = false;
  message: string;
  subscriptions: Subscription;

  scale: number;
  pageHeight: number;
  fitToHeightPortraitScale: number;
  fitToHeightLandscapeScale: number;
  fitToWidthPortrait: boolean;
  fitToWidthLandscape: boolean;
  shouldFitToHeight: boolean;
  isPortrait: boolean;
  isLandscape: boolean;

  Action = Action;

  isFaxMobile = false;

  fieldDocName: FieldDefinition;
  documentName = '';
  lastDocumentName = '';
  showDocumentName = true;
  shouldOpenChangePasswordModal = false;

  get canEditDocumentName(): boolean {
    return this.hasOwnerOrEditSharingPermission && this.documentNameEditingAllowed;
  }

  get redactionAllowed(): boolean {
    return (
      this.userStateService.hasRolePermission(RolePermissions.INTERFAX_PERMISSIONS_ALLOWBLACKOUT) &&
      this.hasOwnerOrEditSharingPermission &&
      this.sessionStorageService.getLoginMode() !== LoginModes.Device
    );
  }

  get hasOwnerOrEditSharingPermission(): boolean {
    return (
      this.hasDocumentSharingPermission(SharePermissions.Edit) ||
      this.hasDocumentSharingPermission(SharePermissions.Owner)
    );
  }

  @HostListener('window:resize') onResize(): void {
    if (!this.fitToWidthPortrait && !this.fitToWidthLandscape && this.viewer) {
      this.pageHeight = 0;
      this.scale = 1;
      this.shouldFitToHeight = true;
    }
  }

  get canPreviewAndDownloadDocument() {
    if (!this.previewDocument) return false;

    const canPreviewAndDownload = this.previewDocument.item.data.extendedDetails.canPreviewAndDownload;
    if (canPreviewAndDownload === undefined) return true;

    return canPreviewAndDownload;
  }

  get previewDocumentPages(): PreviewPage[] {
    if (!this.canPreviewAndDownloadDocument) {
      this.handleShowMessage(this.translate.instant('Product-Item-Preview.PreviewNotAvailable'));
    }

    return this.canPreviewAndDownloadDocument && this.previewDocument?.pages ? this.previewDocument.pages : [];
  }

  constructor(
    private userStateService: UserStateService,
    private translate: TranslateService,
    private actionService: ActionService,
    private userPreferencesService: UserPreferencesService,
    private interfaxService: InterFAXService,
    private spinnerService: SpinnerStateService,
    private errorsService: ErrorsService,
    private toastrService: ToastrService,
    private sessionStorageService: SessionStorageService,
    private apiService: ApiService,
    private delayService: DelayService
  ) {}

  ngOnChanges(changes: SimpleChanges): void {
    if (this.selectedItems.length) {
      if (this.selectedItems.length > 1) {
        this.handleShowMessage(this.translate.instant('Product-Item-Preview.MultipleItemsSelected'));
        return;
      } else {
        if (this.previewDocument == null) {
          this.handleShowMessage(this.translate.instant('Product-Item-Preview.PreviewNotAvailable'));
          return;
        } else {
          this.showMessage = false;
          this.updateShouldOpenChangePasswordModal();
        }
      }
    } else {
      this.handleShowMessage(this.translate.instant('Product-Item-Preview.NoItemsSelected'));
      return;
    }

    if (changes.previewDocument) {
      this.initializeDocNameField();

      if (changes.previewDocument.currentValue) {
        this.currentPage = null;

        this.lastDocumentName = this.documentName = this.previewDocument?.name ?? '';

        if (this.annotations) this.annotations.resetAnnotations();
      } else {
        this.lastDocumentName = this.documentName = '';

        this.handleShowMessage(this.translate.instant('Product-Item-Preview.PreviewNotAvailable'));
        return;
      }
    }

    if (changes.selectedItems?.currentValue) this.handleSelectedItemChanges(changes.selectedItems?.currentValue);
  }

  ngOnInit(): void {
    this.init();
  }

  private init(): void {
    this.subscriptions = new Subscription();
    this.isFaxMobile = this.sessionStorageService.getIsInterFaxMobile();
    this.subscriptions.add(
      this.sessionStorageService.onIsInterFaxMobileChanged.subscribe((x) => (this.isFaxMobile = x))
    );
    this.fitToWidthPortrait = true;
    this.fitToWidthLandscape = false;
    this.isPortrait = true;
    this.isLandscape = false;
    this.scale = 1;
    this.thumbnailsWidthPreferenceKey = this.folderId + 'thumbnails-width';
    this.thumbnailsWidth = this.userPreferencesService.getPreference(this.thumbnailsWidthPreferenceKey);
    this.subscriptions.add(this.actionService.onActivateAction.subscribe(this.invokeAction));

    this.lastDocumentName = this.documentName = this.previewDocument?.name ?? '';
  }

  updateShouldOpenChangePasswordModal() {
    this.shouldOpenChangePasswordModal = false;
    this.delayService.execute(() => {
      this.shouldOpenChangePasswordModal = !this.canPreviewAndDownloadDocument;
    }, 0);
  }

  handleSelectedItemChanges = (items): void => {
    if (!this.previewDocument) return;

    const item = items.find((x) => x.data?.internalID == this.previewDocument.item?.data?.internalID);
    if (!item) return;

    this.previewDocument.name = item.data.documentFilename;
    this.lastDocumentName = this.documentName = this.previewDocument?.name ?? '';
    this.fieldDocName.placeholder = `${this.documentName} ${this.previewDocument?.pageCountText}`;
  };

  hasDocumentSharingPermission = (permission: SharePermissions): boolean => {
    const permissionFlag = this.previewDocument?.item?.data?.extendedDetails.permissionsFlag;
    if (!permissionFlag) return false;

    return (permissionFlag & permission) === permission;
  };

  onDocumentReloadRequired = (): void => {
    this.documentReloadRequired.emit();
  };

  onAnnotationsChanged = (pageNumber): void => {
    this.thumbnails.onAnnotationsLoaded();
  };

  onAnnotationAdded = (annotation: Annotation): void => {
    if (this.thumbnails) this.thumbnails.onAnnotationAdded(annotation);
  };

  onAnnotationUpdated = (annotation: Annotation): void => {
    if (this.thumbnails) this.thumbnails.onAnnotationUpdated(annotation);
  };

  onAnnotationRemoved = (annotation: Annotation): void => {
    if (this.thumbnails) this.thumbnails.onAnnotationRemoved(annotation);
  };

  onApplyAnnotations = (): void => {
    const msg = this.redactionsOnly()
      ? this.translate.instant('annotations.applying-redactions')
      : this.translate.instant('annotations.applying');
    this.spinnerService.show(msg, document.body);
    const allAnnotations: Annotation[] = [];
    this.previewDocument.pages.forEach((page) => allAnnotations.push(...page.annotations));

    this.interfaxService.applyAnnotations(
      this.selectedItems[0].data.id,
      allAnnotations.map((p) => p.serializable()),
      this.annotationsSaveSucceeded,
      this.annotationsSaveFailed
    );
  };

  annotationsSaveSucceeded = (result: ApiResult): void => {
    const msg = this.redactionsOnly()
      ? this.translate.instant('annotations.redactions-applied')
      : this.translate.instant('annotations.succeeded');
    this.toastrService.success(msg);
    this.annotations.resetAnnotations();
    this.onDocumentReloadRequired();
    this.spinnerService.hide();
  };

  annotationsSaveFailed = (err: any): void => {
    const msg = this.redactionsOnly()
      ? this.translate.instant('annotations.redactions-failed')
      : this.translate.instant('annotations.failed');
    this.errorsService.showErrorsModal([err], msg);
    this.spinnerService.hide();
  };

  redactionsOnly = (): boolean => {
    return this.annotations.annotations.findIndex((x) => x.annotationType != AnnotationTypes.Redaction) == -1;
  };

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  invokeAction = (actionEvent: ActionEvent): void => {
    if (!this.currentPage) return;

    if (actionEvent.action == Action.FitToHeight) {
      this.fit(FitToMeasurement.height);
    } else if (actionEvent.action == Action.FitToWidth) {
      this.fit(FitToMeasurement.width);
    } else if (actionEvent.action == Action.ZoomIn) {
      this.zoomIn();
    } else if (actionEvent.action == Action.ZoomOut) {
      this.zoomOut();
    }
  };

  handleShowMessage(message: string): void {
    this.previewDocument = null;
    this.currentPage = null;
    this.showMessage = true;
    this.message = message;
  }

  onWidthResize(p: Preference): void {
    this.userPreferencesService.setPreference(p.name, p.value);

    if (this.thumbnails) {
      this.thumbnails.widthResized();
    }

    if (this.fitToWidthPortrait) {
      this.updatePreviewSize();
    }
  }

  updatePreviewSize(): void {
    if (this.previewDocument && this.viewer) {
      this.viewer.updateSize();
    }
  }

  isPDF(file: Blob): boolean {
    return true;

    // for now everything is PDF

    //return file.type === 'text/xml' || file.type === 'application/pdf';
  }

  setCurrentPage(page: PreviewPage): void {
    if (!this.isPDF(page.originalFile)) {
      this.showMessage = true;
      this.message = `${this.translate.instant('Product-Item-Preview.UnsupportedFileTypeError')} ${page.fileName}`;
      return;
    }

    if (this.currentPage) {
      this.currentPage.selected = false;
    }

    this.currentPage = page;
    this.currentPage.selected = true;
    this.directiveRef?.scrollToTop();
    this.directiveRef?.scrollToLeft();
  }

  onTextLayerRendered = (event: any): void => {
    this.getPageHeight(event);
    if (this.annotations) this.annotations.onPageRendered(event);
  };

  getPageHeight(event: any, resetHeight?: boolean): void {
    if (!this.pageHeight) {
      this.pageHeight = event.source.div.offsetHeight;
    }

    this.setScalesForFitToHeight(this.shouldFitToHeight);
  }

  setScalesForFitToHeight(andFitToHeight: boolean): void {
    const viewerWrapper = document.querySelector('.viewer') as HTMLElement;
    let percentage: number;
    percentage = ((viewerWrapper.offsetHeight - 40) / this.pageHeight) * 100;
    this.fitToHeightPortraitScale = parseFloat((percentage * 0.01).toFixed(2));
    percentage = ((viewerWrapper.offsetHeight - 45) / this.pageHeight) * 100;
    this.fitToHeightLandscapeScale = parseFloat((percentage * 0.01).toFixed(2));
    if (andFitToHeight) {
      this.fit(FitToMeasurement.height);
      this.shouldFitToHeight = false;
    }
  }

  fit(to: FitToMeasurement): void {
    if (to === FitToMeasurement.width) {
      this.viewer.updateSize();
      this.scale = 1;
      if (this.isPortrait) {
        this.fitToWidthPortrait = true;
        this.fitToWidthLandscape = false;
      } else {
        this.fitToWidthPortrait = false;
        this.fitToWidthLandscape = true;
      }
    } else if (to === FitToMeasurement.height) {
      this.viewer.updateSize();
      this.fitToWidthPortrait = false;
      this.fitToWidthLandscape = false;
      if (this.isPortrait) {
        this.scale = this.fitToHeightPortraitScale;
      } else {
        this.scale = this.fitToHeightLandscapeScale;
      }
    }

    this.directiveRef?.update();
  }

  zoomIn(): void {
    if (this.scale >= VIEWER_CONFIG.maxScale) {
      return;
    }

    this.scale = Math.round((this.scale + VIEWER_CONFIG.scaleIncrement) * 100) / 100;
  }

  zoomOut(): void {
    if (this.scale <= VIEWER_CONFIG.minScale) {
      return;
    }

    this.scale = Math.round((this.scale - VIEWER_CONFIG.scaleIncrement) * 100) / 100;
  }

  onSaveDocumentName = (newDocName: string): void => {
    this.documentName = newDocName;

    if (newDocName.trim() === '') {
      this.onDocumentNameChangeFailed(this.translate.instant('Product-Item-Preview.EmptyFileName'));
      this.restoreLastSavedDocumentName();
      return;
    }

    if (!this.previewDocument) return;
    if (this.previewDocument.name === newDocName) return;

    this.spinnerService.show(this.translate.instant('Product-Item-Preview.RenameInProgress'), document.body);

    this.apiService.renameItem(
      this.previewDocument.item.data.id,
      newDocName,
      (newName) => this.onDocumentNameChangeSuccessful(newName),
      (err) => this.onDocumentNameChangeFailed(err)
    );
  };

  restoreLastSavedDocumentName(): void {
    if (!this.previewDocument) return;

    this.documentName = this.previewDocument.name = this.lastDocumentName;
    this.rerenderDocumentNameField();
  }

  rerenderDocumentNameField(): void {
    this.showDocumentName = false;
    this.delayService.execute(() => (this.showDocumentName = true), 25);
  }

  onDocumentNameChangeSuccessful = (newDocName: string): void => {
    if (!this.previewDocument) return;

    this.lastDocumentName = this.documentName = this.previewDocument.name = newDocName;
    this.initializeDocNameField();
    this.documentReloadRequired.emit();
    this.spinnerService.hide();
  };

  onDocumentNameChangeFailed = (err): void => {
    this.spinnerService.hide();

    this.errorsService.showErrorsModal(err, this.translate.instant('Product-Item-Preview.RenameFailed'));
  };

  initializeDocNameField = (): void => {
    this.fieldDocName = new FieldDefinition({
      type: MetaDataType.Text,
      infoText: this.previewDocument?.pageCountText,
    });
  };

  onPasswordUpdated() {
    this.onDocumentReloadRequired();
  }
}

export const VIEWER_CONFIG = {
  maxScale: 5,
  minScale: 0.1,
  scaleIncrement: 0.1,
};

export class PreviewDocument {
  name: string;
  pageCount: number;
  pageCountText: string;
  pages: Array<PreviewPage>;
  currentPage = 1;

  constructor(public item: TableItem, public datasource: ItemTableDataSource, private translate: TranslateService) {
    this.name = item.data.documentFilename;
    this.pageCount = item.data.pageCount;
    this.pageCountText = Filename.getPageCountText(this.pageCount, translate);
    this.pages = [];
    for (let i = 1; i <= this.pageCount; i++) {
      this.pages.push(new PreviewPage(i, datasource, item));
    }
  }
}

export class PreviewPage {
  id: string;
  pageNumber: number;
  originalFile: Blob;
  url: string;
  thumbnailUrl: string;
  state: PageState;
  selected: boolean;
  fileName: string;
  annotations: Annotation[] = [];
  barcodes: InboundLogBarcode[] = [];
  annotationsChanged = new Subject<void>();

  constructor(pageNumber: number, public datasource: ItemTableDataSource, public item: TableItem) {
    this.id = Date.now().toString() + Math.floor(Math.random() * 100);
    this.pageNumber = pageNumber;
    this.state = PageState.Empty;
    this.fileName = item.data.documentFilename;

    if (!item.data.extendedDetails?.barcodes) return;
    this.barcodes = item.data.extendedDetails.barcodes
      .filter((x) => x.page == pageNumber)
      .map((x) => new InboundLogBarcode(x));
  }

  getPage(): Promise<void> {
    return new Promise((resolve, reject) => {
      this.datasource.getPage(
        this.item,
        this.pageNumber,
        (blob: Blob) => {
          this.originalFile = blob;
          this.url = window.URL.createObjectURL(blob);
          resolve();
        },
        (err) => {
          reject(err);
        }
      );
    });
  }

  onAnnotationsChanged = (): void => {
    this.annotationsChanged.next();
  };
}
