import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { Subscription } from 'rxjs';
import { FieldDefinition, MetaDataType } from 'shared-code-lib';
import { Filter } from '../../models/filter';
import { FilterBaseComponent } from '../filter-base.component';

@Component({
  selector: 'app-filter-by-date-range',
  templateUrl: './filter-by-date-range.component.html',
  styleUrls: ['./filter-by-date-range.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FilterByDateRangeComponent extends FilterBaseComponent implements OnInit, OnDestroy {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  value = '';

  fieldDateRange: FieldDefinition;

  subscriptions = new Subscription();

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  ngOnInit(): void {
    this.createFields();

    this.initializeFilter();
    this.subscriptions.add(this.filter.clearFilter.subscribe(this.clearFilter));
  }

  setValue = (val: string): void => void (this.value = this.filter.value = val);

  createFields = (): void => {
    this.fieldDateRange = new FieldDefinition({
      type: MetaDataType.DateRange,
      label: '',
      placeholder: '',
      required: false,
      smallLabel: this.filter.label,
    });
  };

  onDateRangeChanged = (value: string): void => {
    this.filter.value = value;
    this.selected.emit(this.filter.value);
  };
}
