import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { IFieldOptions } from 'shared-code-lib';
import { Log } from '../../../../app/blocks/core/log';
import { FieldTypesDataSource } from '../../../../app/views/admin-metadata/admin-metadata.component';
import { Filter } from '../../models/filter';
import { UserStateService } from '../../services/user-state.service';

@Component({
  selector: 'app-filter-by-metadata-type',
  templateUrl: './filter-by-metadata-type.component.html',
  styleUrls: ['./filter-by-metadata-type.component.scss'],
})
export class FilterByMetaDataTypeComponent implements OnInit {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();
  options: Array<IFieldOptions> = [];

  constructor(
    private translate: TranslateService,
    private toastr: ToastrService,
    private userStateService: UserStateService
  ) {}

  ngOnInit(): void {
    this.options.push({ name: this.translate.instant(`filter.filter-by-metadata-type.none`), value: '' });
    new FieldTypesDataSource(this.translate, this.userStateService).getItems(
      '',
      '',
      this.onFieldTypesLoaded,
      this.onFieldTypesLoadFailed
    );
  }

  onFieldTypesLoaded = (items: IFieldOptions[]) => {
    items.forEach((x) => this.options.push({ name: x.name, value: x.value }));
  };

  onFieldTypesLoadFailed = (err) => {
    this.toastr.error(this.translate.instant('filter-by-metadata-type.items-load-failed'));
    Log.error('filter-by-metadata-type.items-load-failed', err);
  };

  onFilterSelected = (value: string) => this.selected.emit(value);
}
