import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

import { FixedDataSource, IFieldOptions, RolePermissions } from 'shared-code-lib';
import { Group } from '../../InterFAX/Models/admin';
import { Filter } from '../../models/filter';
import { InterFAXService } from '../../services/interfax.service';
import { UserStateService } from '../../services/user-state.service';

@Component({
  selector: 'app-filter-by-interfax-inbox-shared-with',
  templateUrl: './filter-by-interfax-inbox-shared-with.component.html',
  styleUrls: ['./filter-by-interfax-inbox-shared-with.component.scss'],
})
export class FilterByInterfaxInboxSharedWithComponent implements OnInit {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  dataSource: FixedDataSource;

  constructor(
    private interfaxService: InterFAXService,
    private translateService: TranslateService,
    private userState: UserStateService
  ) {}

  ngOnInit(): void {
    this.dataSource = new InterFAXInboxSharedWithDataSource(
      this.interfaxService,
      this.translateService,
      this.userState
    );
  }

  onFilterSelected = (value: string) => this.selected.emit(value);
}

export class InterFAXInboxSharedWithDataSource extends FixedDataSource {
  constructor(
    private interfaxService: InterFAXService,
    private translate: TranslateService,
    private userState: UserStateService
  ) {
    super();
  }

  successCallBack: (items: IFieldOptions[]) => void;
  failureCallBack: (err: any) => void;

  getItems(
    startsWith: string,
    currentValue: string,
    successCallBack: (items: IFieldOptions[]) => void,
    failureCallBack: (err: any) => void
  ): void {
    this.successCallBack = successCallBack;
    this.failureCallBack = failureCallBack;

    if (this.userState.hasRolePermission(RolePermissions.INTERFAX_ADMINISTRATOR)) {
      this.interfaxService.getGroups(this.groupsLoaded, this.groupsLoadFailed);
      return;
    }

    this.interfaxService.getUserGroups(this.groupsLoaded, this.groupsLoadFailed);
  }

  groupsLoaded = (groups: Array<Group>) => {
    groups.sort((x, y) => x.name.localeCompare(y.name));

    const options = new Array<IFieldOptions>();
    options.push({ name: this.translate.instant('filter-by-interfax-inbox-shared-with.all'), value: '' });

    options.push({
      name: this.translate.instant('filter-by-interfax-inbox-shared-with.sharedtomedirectly'),
      value: 'sharedtomedirectly',
    });

    options.push({
      name: this.translate.instant('filter-by-interfax-inbox-shared-with.sharedtoeveryone'),
      value: 'sharedtoeveryone',
    });

    groups.forEach((g) => options.push({ name: g.name, value: g.id }));

    this.successCallBack(options);
  };

  groupsLoadFailed = (err: any) => this.failureCallBack(err);
}
