import { Component, EventEmitter, Input, OnChanges, Output, SimpleChanges } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import moment from 'moment';
import { Filter } from '../../models/filter';

@Component({
  selector: 'app-filter-by-olc-date',
  templateUrl: './filter-by-olc-date.component.html',
  styleUrls: ['./filter-by-olc-date.component.scss'],
})
export class FilterByOLCDateComponent implements OnChanges {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  constructor(private translateService: TranslateService) {}

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.filter) {
      this.filter.label = this.translateService.instant(this.filter.label);
    }
  }

  onFilterChanged = (value: string): void => {
    if (value)
      value = value
        .split(':')
        .map((d) => this.convertDateToUTc(d))
        .join(';');

    this.selected.emit(value);
  };

  convertDateToUTc(date: string) {
    const localDate = moment(date, 'YYYY-MM-DD').startOf('day');
    return moment.utc(localDate).format('YYYY-MM-DDTHH:mm:ss[Z]');
  }
}
