import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Subscription } from 'rxjs';
import { FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType } from 'shared-code-lib';
import { Filter } from '../../models/filter';
import { FilterBaseComponent } from '../filter-base.component';

@Component({
  selector: 'app-filter-by-interfax-transaction-status',
  templateUrl: './filter-by-interfax-transaction-status.component.html',
  styleUrls: ['./filter-by-interfax-transaction-status.component.scss'],
})
export class FilterByInterfaxTransactionStatusComponent extends FilterBaseComponent implements OnInit, OnDestroy {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  fieldTransactionStatus: FieldDefinition;
  status = '';

  subscriptions = new Subscription();

  constructor(private translateService: TranslateService) {
    super();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  ngOnInit(): void {
    this.createFields();

    this.initializeFilter();
    this.subscriptions.add(this.filter.clearFilter.subscribe(this.clearFilter));
  }

  clearFilter = (): void => this.setValue('*');

  setValue = (val: string): void => void (this.status = this.filter.value = val);

  onStatusChanged = (status: string): void => {
    if (this.filter.value == status) return;

    this.filter.value = status;
    this.selected.next(this.filter.value);
  };

  createFields = (): void => {
    const statusOptions = [];

    this.addStatus(statusOptions, '*');
    this.addStatus(statusOptions, 'open');

    this.fieldTransactionStatus = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      placeholder: '',
      required: false,
      datasource: new FixedDataSource(statusOptions),
      smallLabel: this.translateService.instant('filter.transaction-status'),
    });
  };

  addStatus = (statusOptions: Array<IFieldOptions>, value: string): void => {
    const text = this.translateService.instant(`filter-by-interfax-transaction-status.${value}`);
    statusOptions.push({ name: text, value: value });
  };
}
