import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Subscription } from 'rxjs';
import { FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType } from 'shared-code-lib';
import { Filter } from '../../models/filter';
import { FilterBaseComponent } from '../filter-base.component';

@Component({
  selector: 'app-filter-by-contact-property',
  templateUrl: './filter-by-contact-property.component.html',
  styleUrls: ['./filter-by-contact-property.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FilterByContactPropertyComponent extends FilterBaseComponent implements OnInit, OnDestroy {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  contactProperty = '';
  contains = '';

  fieldContactProperty: FieldDefinition;
  fieldContains: FieldDefinition;

  timer = null;
  lastFilterValue: string;

  contactPropertyDataSource = new FixedDataSource(new Array<IFieldOptions>());

  subscriptions = new Subscription();

  constructor(private translateService: TranslateService) {
    super();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  ngOnInit(): void {
    this.createFields();

    this.addOption('name');
    this.addOption('fax-number');
    this.addOption('company');

    this.initializeFilter();
    this.subscriptions.add(this.filter.clearFilter.subscribe(this.clearFilter));
  }

  clearFilter = (): void => this.setValue('name:');

  setValue = (val: string): void => {
    setTimeout(() => {
      const parts = this.getFilterParts(val);
      if (parts.length < 2) return;

      this.contactProperty = parts[0];
      this.contains = parts[1];

      this.onFieldChanged();
    }, 50);
  };

  addOption = (cp: string): void => {
    this.contactPropertyDataSource.options.push({
      name: this.translateService.instant(`filter-by-contact-property.${cp}`),
      value: cp,
    });
  };

  onContactPropertyChanged = (cp: string): void => {
    this.contactProperty = cp;

    this.onFieldChanged();
  };

  onContainsChanged = (contains: string): void => {
    this.contains = contains;

    this.onFieldChanged();
  };

  onFieldChanged = (): void => {
    this.setFilterValue();

    if (this.timer) clearTimeout(this.timer);
    this.timer = setTimeout(this.fireFieldChanged, 250);
  };

  fireFieldChanged = (): void => {
    if (this.lastFilterValue !== this.filter.value) {
      this.selected.emit(this.filter.value);
      this.lastFilterValue = this.filter.value;
    }
  };

  setFilterValue = (): void => {
    this.filter.value = this.contactProperty ? `${this.contactProperty}:${this.contains}` : '';
  };

  createFields = (): void => {
    this.fieldContactProperty = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('filter-by-contact-property.property'),
      placeholder: '',
      required: false,
      datasource: this.contactPropertyDataSource,
      hideLabel: true,
    });

    this.fieldContains = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('filter-by-contact-property.property-value'),
      placeholder: '',
      required: false,
      hideLabel: true,
    });
  };
}
