import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { BehaviorSubject, Subscription } from 'rxjs';
import { debounceTime, filter, map, pairwise, startWith } from 'rxjs/operators';
import { ICombinedColumn, TableItem } from 'shared-code-lib';
import { IFilterValue } from '../models/filter';
import {
  AutoExecuteResult,
  InterFaxActions,
  ItemActionHandler,
  SilentUpdateResult,
  UpdateResults,
} from './../../blocks/core/itemActionHandler';
import { ExecuteCriteria, IFolder } from './models';

@Injectable({
  providedIn: 'root',
})
export class AutoExecuteActionsService {
  private _selectedItemSubject = new BehaviorSubject<TableItem>(null);
  selectedItem$ = this._selectedItemSubject.asObservable();

  constructor(private translate: TranslateService) {}

  setSelectedItem = (item: TableItem) => {
    this._selectedItemSubject.next(item);
  };

  setupAutoExecuteActions = (
    autoExecuteResult: AutoExecuteResult,
    currentFolder: IFolder,
    itemActionHandler: ItemActionHandler
  ): Subscription => {
    const autoExecuteSubscriptions = new Subscription();

    autoExecuteResult.actions.forEach((action) => {
      let subscription: Subscription;

      switch (action.executeWhen) {
        case ExecuteCriteria.OnSelection:
          subscription = this.selectedItem$
            .pipe(filter((item) => this.isAutoExecuteActionAllowed(action.id, item)))
            .subscribe((item) =>
              this.invokeAutoExecuteAction(action.id, [{ id: item.data.id }], currentFolder, itemActionHandler)
            );
          break;
        case ExecuteCriteria.TimeAfterSelection:
          subscription = this.selectedItem$
            .pipe(
              debounceTime(5000),
              filter((item) => this.isAutoExecuteActionAllowed(action.id, item))
            )
            .subscribe((item) =>
              this.invokeAutoExecuteAction(action.id, [{ id: item.data.id }], currentFolder, itemActionHandler)
            );
          break;
        case ExecuteCriteria.DeSelected:
          subscription = this.selectedItem$
            .pipe(
              startWith(null),
              pairwise(),
              map(([prevD]) => prevD),
              filter((item) => this.isAutoExecuteActionAllowed(action.id, item))
            )
            .subscribe((item) =>
              this.invokeAutoExecuteAction(action.id, [{ id: item.data.id }], currentFolder, itemActionHandler)
            );
          break;
        case ExecuteCriteria.NoOp:
        default:
          break;
      }

      if (subscription) autoExecuteSubscriptions.add(subscription);
    });

    return autoExecuteSubscriptions;
  };

  private invokeAutoExecuteAction = (
    actionId: string,
    items: { id: string }[],
    currentFolder: IFolder,
    itemActionHandler: ItemActionHandler
  ) => {
    itemActionHandler.invoke(
      currentFolder.id,
      currentFolder.actions.find((a) => a.id == actionId),
      items,
      this.getFolderFilters(currentFolder),
      true,
      true
    );
  };

  private getFolderFilters = (folder: IFolder): IFilterValue[] => {
    return folder.filters.map((filter) => {
      return { id: filter.id, value: filter.value };
    });
  };

  private isAutoExecuteActionAllowed = (actionId: string, item: TableItem) => {
    if (!item || !actionId) return false;
    if (actionId !== InterFaxActions.MarkRead) return true;
    return !item?.data.isRead;
  };

  silentlyUpdateItems(
    ur: UpdateResults,
    tableItems: Array<TableItem>,
    selectedItems: Array<TableItem>,
    columns: Array<ICombinedColumn>,
    expandedItems: Array<{ id: string }> = null
  ): SilentUpdateResult {
    ur.updated.forEach((t) => {
      const itemIndex = tableItems.findIndex((i) => i.data.id == t.id);
      if (itemIndex == -1) return;

      let data = ur.inflater ? ur.inflater(tableItems[itemIndex].data) : t;

      tableItems[itemIndex] = new TableItem(data, columns, this.translate);
    });

    expandedItems?.forEach((ei) => {
      const itemIndex = tableItems.findIndex((ti) => ti.data.id == ei.id);
      if (itemIndex == -1) return;

      tableItems[itemIndex].expanded = true;
    });

    tableItems = [...tableItems];
    selectedItems = tableItems
      .filter((ti) => selectedItems.some((si) => si.data.id == ti.data.id))
      .map((ti) => {
        ti.selected = true;
        return ti;
      });

    return { tableItems, selectedItems };
  }
}
