import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Subscription } from 'rxjs';
import { FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType, Strings } from 'shared-code-lib';
import { Log } from '../../core/log';
import { DataSource } from '../../models/datasource';
import { Filter } from '../../models/filter';
import { ErrorsService } from '../../services/errors.service';
import { HydraApiService } from '../../services/hydra-api.service';
import { UserStateService } from '../../services/user-state.service';
import { FilterBaseComponent } from '../filter-base.component';

@Component({
  selector: 'app-filter-by-datasource-column',
  templateUrl: './filter-by-datasource-column.component.html',
  styleUrls: ['./filter-by-datasource-column.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FilterByDataSourceColumnComponent extends FilterBaseComponent implements OnInit, OnDestroy {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  columnId = '';
  contains = '';
  fieldColumn: FieldDefinition;
  fieldContains: FieldDefinition;
  folderId: string = 'BambaSettingsApplication-Upland.Bamba.Application.Settings.DataSourceRows';
  datasource: DataSource;
  columnsDataSource = new FixedDataSource(new Array<IFieldOptions>());
  subscriptions = new Subscription();

  constructor(
    private translateService: TranslateService,
    private userStateService: UserStateService,
    private hydraApi: HydraApiService,
    private errorsService: ErrorsService
  ) {
    super();
  }

  ngOnInit(): void {
    const folder = this.userStateService.findFolderById(this.folderId);
    if (folder) {
      if (folder.parentFolderId) {
        this.loadDataSource(folder.parentFolderId);
      } else {
        this.subscriptions.add(
          this.userStateService.userChanged.subscribe((user) => {
            if (!user) return;

            const folder = this.userStateService.findFolderById(this.folderId);
            if (folder.parentFolderId) {
              this.loadDataSource(folder.parentFolderId);
            }
          })
        );
      }
    } else {
      this.errorsService.showError('admin-datasource.invalid-datasource', '');
    }

    this.initializeFilter();
    this.subscriptions.add(this.filter.clearFilter.subscribe(this.clearFilter));
  }

  clearFilter = (): void => this.setValue(':');

  setValue = (val: string): void => {
    const parts = this.getFilterParts(val);
    if (parts.length < 2) return;

    this.columnId = parts[0];
    this.contains = parts[1];

    this.setFilterValue();
  };

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  loadDataSource = (datasourceId: string): void => {
    this.hydraApi.getDataSource(datasourceId, this.datasourceLoadSucceeded, this.datasourceLoadFailed);
  };

  datasourceLoadSucceeded = (datasource: DataSource): void => {
    this.datasource = datasource;

    this.datasource.columns.forEach((column) => {
      this.columnsDataSource.options.push({
        name: column.name,
        value: column.id,
      });
    });

    this.columnsDataSource.options.unshift({
      name: this.translateService.instant('filter.filter-by-datasource-column-none'),
      value: '',
    });

    this.columnsDataSource.options.sort((x, y) => x.name.toLowerCase().localeCompare(y.name.toLowerCase()));

    this.createFields();
  };

  datasourceLoadFailed = (error: any): void => {
    Log.error('admin-datasource.load-failed', error);
  };

  onColumnChanged = (columnId: string): void => {
    this.columnId = columnId;
    if (Strings.notEmpty(this.contains)) this.onFieldChanged();
  };

  onContainsChanged = (contains: string): void => {
    this.contains = contains;
    this.onFieldChanged();
  };

  onFieldChanged = (): void => {
    this.setFilterValue();
    this.selected.emit(this.filter.value);
  };

  setFilterValue = (): void => {
    this.filter.value = this.columnId ? `${this.columnId}:${this.contains}` : '';
  };

  createFields = (): void => {
    this.fieldColumn = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-datasource.filter-by-datasource-column'),
      placeholder: '',
      hideLabel: true,
      required: false,
      datasource: this.columnsDataSource,
    });

    this.fieldContains = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-datasource.datasource-column-search-text'),
      placeholder: '',
      hideLabel: true,
      required: false,
    });
  };
}
