import { Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { IFieldOptions } from 'shared-code-lib';
import { Log } from '../../core/log';
import { Filter } from '../../models/filter';
import { HydraApiService } from '../../services/hydra-api.service';
import { User } from '../../services/models';

@Component({
  selector: 'app-filter-by-olc-user',
  templateUrl: './filter-by-olc-user.component.html',
  styleUrls: ['./filter-by-olc-user.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FilterByOLCUserComponent implements OnInit {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  options: Array<IFieldOptions> = [];

  constructor(private hydraApi: HydraApiService, private translate: TranslateService, private toastr: ToastrService) {}

  ngOnInit(): void {
    this.hydraApi.getUsers(this.onUsersLoaded, this.onUsersLoadFailed);
  }

  onUsersLoaded = (users: Array<User>): void => {
    const usersOptions = users.map((u) => {
      return { name: u.name, value: u.id };
    });

    usersOptions.sort((x, y) => x.name.toLowerCase().localeCompare(y.name.toLowerCase()));

    this.options = [{ name: this.translate.instant('admin-olc-filters.all'), value: '' }, ...usersOptions];
  };

  onUsersLoadFailed = (err: any): void => {
    this.toastr.error(this.translate.instant('admin-olc-filters.users-load-failed'));
    Log.error('FilterByOLCTemplateVersionCreatorUserComponent: Failed to load users', err);
  };

  onFilterSelected = (value: string) => this.selected.emit(value);
}
