import { Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { IFieldOptions } from 'shared-code-lib';
import { Log } from '../../core/log';
import { Filter } from '../../models/filter';
import { Workflow } from '../../models/workflow';
import { HydraApiService } from '../../services/hydra-api.service';

@Component({
  selector: 'app-filter-by-metadata-reference',
  templateUrl: './filter-by-metadata-reference.component.html',
  styleUrls: ['./filter-by-metadata-reference.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FilterByMetaDataReferenceComponent implements OnInit {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  options: Array<IFieldOptions> = [];

  constructor(private hydraApi: HydraApiService, private translate: TranslateService, private toastr: ToastrService) {}

  ngOnInit(): void {
    this.options.unshift({ name: this.translate.instant('filter.filter-by-metadata-reference.all'), value: '' });
    this.hydraApi.getWorkflows(this.onWorkflowsLoaded, this.onWorkflowsLoadFailed);
  }

  onWorkflowsLoaded = (workflows: Array<Workflow>): void => {
    this.options = workflows.map((w) => {
      return { name: w.name, value: w.id };
    });
    this.options.sort((x, y) => x.name.toLowerCase().localeCompare(y.name.toLowerCase()));

    this.options.unshift({
      name: this.translate.instant('filter.filter-by-metadata-reference.unreferenced'),
      value: 'Unreferenced',
    });

    this.options.unshift({
      name: this.translate.instant('filter.filter-by-metadata-reference.referenced'),
      value: 'Referenced',
    });

    this.options.unshift({ name: this.translate.instant('filter.filter-by-metadata-reference.all'), value: '' });
  };

  onWorkflowsLoadFailed = (err: any): void => {
    this.toastr.error(this.translate.instant('filter.filter-by-metadata-reference.items-load-failed'));
    Log.error('FilterByMetaDataReferenceComponent Failed to load workflows', err);
  };

  onFilterSelected = (value: string) => this.selected.emit(value);
}
