import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Subscription } from 'rxjs';
import { FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType } from 'shared-code-lib';
import { Filter } from '../../models/filter';
import { FilterBaseComponent } from '../filter-base.component';

@Component({
  selector: 'app-filter-by-interfax-date-selection',
  templateUrl: './filter-by-interfax-date-selection.component.html',
  styleUrls: ['./filter-by-interfax-date-selection.component.scss'],
})
export class FilterByInterfaxDateSelectionComponent extends FilterBaseComponent implements OnInit, OnDestroy {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  field: FieldDefinition;
  range = 'today';

  subscriptions = new Subscription();

  constructor(private translateService: TranslateService) {
    super();
  }

  ngOnInit(): void {
    this.createField();

    this.initializeFilter();
    this.subscriptions.add(this.filter.clearFilter.subscribe(this.clearFilter));
  }

  clearFilter = (): void => this.setValue('today');

  setValue = (val: string): void => void (this.filter.value = this.range = val);

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  createField(): void {
    const rangeOptions = [];

    this.addRange(rangeOptions, 'today');
    this.addRange(rangeOptions, 'yesterday');
    this.addRange(rangeOptions, 'this-week');
    this.addRange(rangeOptions, 'last-week');
    this.addRange(rangeOptions, 'this-month');

    this.field = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      placeholder: '',
      required: false,
      datasource: new FixedDataSource(rangeOptions),
    });
  }

  addRange = (rangeOptions: Array<IFieldOptions>, value: string): void => {
    const text = this.translateService.instant(`filter-by-interfax-date-selection.${value}`);
    rangeOptions.push({ name: text, value: value });
  };

  onChanged(value: any): void {
    this.filter.value = value;
    this.selected.next(this.filter.value);
  }
}
