import { Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { IFieldOptions } from 'shared-code-lib';
import { Filter } from '../../models/filter';

@Component({
  selector: 'app-filter-by-options',
  templateUrl: './filter-by-options.component.html',
  styleUrls: ['./filter-by-options.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FilterByOptionsComponent implements OnInit {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  options: IFieldOptions[] = [];

  ngOnInit(): void {
    this.options = this.filter.options.map((option) => ({ name: option.key, value: option.value }));
  }

  onFilterSelected = (value: string) => this.selected.emit(value);
}
