import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Subscription } from 'rxjs';
import { DelayService, FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType, Tenant } from 'shared-code-lib';
import { Log } from '../../core/log';
import { Filter } from '../../models/filter';
import { HydraApiService } from '../../services/hydra-api.service';
import { FilterBaseComponent } from '../filter-base.component';

@Component({
  selector: 'app-filter-by-tenant',
  templateUrl: './filter-by-tenant.component.html',
  styleUrls: ['./filter-by-tenant.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FilterByTenantComponent extends FilterBaseComponent implements OnInit, OnDestroy {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  fieldTenants: FieldDefinition;

  tenants: Array<Tenant>;
  tenantId: string = '';
  tenantsDataSource = new FixedDataSource(new Array<IFieldOptions>());

  subscriptions = new Subscription();

  constructor(private hydraApi: HydraApiService, private translate: TranslateService, private delay: DelayService) {
    super();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  ngOnInit() {
    this.tenantsDataSource.options.unshift({
      name: this.translate.instant('filter.filter-by-tenant-all'),
      value: '',
    });

    this.createFields();

    this.hydraApi.getTenants(this.onTenantsLoaded, (err: any) =>
      Log.error('FilterByTenantComponent Failed to load tenants', err)
    );

    this.initializeFilter();
    this.subscriptions.add(this.filter.clearFilter.subscribe(this.clearFilter));
  }

  clearFilter = (): void => this.setValue('');

  setValue = (val: string): void => void (this.tenantId = this.filter.value = val);

  onTenantsLoaded = (tenants: Array<Tenant>): void => {
    this.tenants = tenants;

    const items = tenants.map((t) => {
      return { name: t.name, value: t.id };
    });

    items.sort((x, y) => x.name.toLowerCase().localeCompare(y.name.toLowerCase()));
    items.unshift({ name: this.translate.instant('filter.filter-by-tenant-none'), value: '000000000000000000000000' });
    items.unshift({ name: this.translate.instant('filter.filter-by-tenant-all'), value: '' });

    this.tenantsDataSource.updateOptions(items);

    const value = this.filter.value;
    if (value) {
      this.delay.execute(() => {
        this.tenantId = value;
      }, 5);
    }
  };

  createFields = (): void => {
    this.fieldTenants = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      placeholder: '',
      required: false,
      datasource: this.tenantsDataSource,
    });
  };

  onTenantChanged = (tId: string): void => {
    this.tenantId = tId;
    this.onFieldChanged();
  };

  onFieldChanged = (): void => {
    this.filter.value = this.tenantId;
    this.selected.emit(this.filter.value);
  };
}
