import { TranslateService } from '@ngx-translate/core';
import { Subject } from 'rxjs';

import { IFieldDataSource, IFieldOptions } from 'shared-code-lib';

export class VersionStatusDataSource implements IFieldDataSource {
  changed = new Subject<void>();

  types: IFieldOptions[] = [
    { value: '0', name: this.translate.instant('OLCTemplateVersionStatusEnum.Draft') },
    { value: '1', name: this.translate.instant('OLCTemplateVersionStatusEnum.PendingApproval') },
    { value: '2', name: this.translate.instant('OLCTemplateVersionStatusEnum.Approved') },
    { value: '3', name: this.translate.instant('OLCTemplateVersionStatusEnum.Rejected') },
    { value: '4', name: this.translate.instant('OLCTemplateVersionStatusEnum.PendingProduction') },
    { value: '5', name: this.translate.instant('OLCTemplateVersionStatusEnum.InProduction') },
    { value: '6', name: this.translate.instant('OLCTemplateVersionStatusEnum.WasInProduction') },
  ];

  constructor(private translate: TranslateService) {}

  getItems(
    startsWith: string,
    currentValue: string,
    successCallBack: (items: IFieldOptions[]) => void,
    failureCallBack: (err: any) => void
  ): void {
    successCallBack(this.types);
  }
}
