import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IFieldOptions } from 'shared-code-lib';
import { Filter } from '../../models/filter';

@Component({
  selector: 'app-filter-by-recipients',
  templateUrl: './filter-by-recipients.component.html',
  styleUrls: ['./filter-by-recipients.component.scss'],
})
export class FilterByRecipientsComponent implements OnInit {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  options: IFieldOptions[] = [];

  constructor() {}

  ngOnInit(): void {
    this.options = this.filter.options!.map((option) => ({ name: option.key, value: option.value }));
  }

  onFilterSelected = (value: string) => this.selected.emit(value);
}
