import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { Subscription } from 'rxjs';
import { FieldDefinition } from 'shared-code-lib';
import { Filter } from '../../models/filter';
import { FilterBaseComponent } from '../filter-base.component';

@Component({
  selector: 'app-filter-by-checkbox',
  templateUrl: './filter-by-checkbox.component.html',
  styleUrls: ['./filter-by-checkbox.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FilterByCheckboxComponent extends FilterBaseComponent implements OnInit, OnDestroy {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  fieldBrands: FieldDefinition;

  subscriptions = new Subscription();

  get filterValue() {
    return Boolean(this.filter.value);
  }
  set filterValue(val: boolean) {
    this.filter.value = `${val}`;
  }

  ngOnInit() {
    this.initializeFilter();

    this.subscriptions.add(this.filter.clearFilter.subscribe(this.clearFilter));
  }

  clearFilter = (): void => this.setValue(this.filter.defaultValue ?? false.toString());

  filterChanged = (value: boolean): void => {
    this.filter.value = value as any;
    this.selected.emit(this.filter.value);
  };

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
