import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

import { Subscription } from 'rxjs';
import { FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType } from 'shared-code-lib';
import { Filter } from '../../models/filter';
import { FilterBaseComponent } from '../filter-base.component';

@Component({
  selector: 'app-filter-by-relative-date',
  templateUrl: './filter-by-relative-date.component.html',
  styleUrls: ['./filter-by-relative-date.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FilterByRelativeDateComponent extends FilterBaseComponent implements OnInit, OnDestroy {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  fieldDateSelection: FieldDefinition;

  subscriptions = new Subscription();

  constructor(private translate: TranslateService) {
    super();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  ngOnInit() {
    const selections = new Array<IFieldOptions>();

    selections.push({ name: this.translate.instant('filter.filter-by-date-submitted-none'), value: '' });
    selections.push({ name: this.translate.instant('filter.filter-by-date-today'), value: 'today' });
    selections.push({ name: this.translate.instant('filter.filter-by-date-yesterday'), value: 'yesterday' });
    selections.push({ name: this.translate.instant('filter.filter-by-date-this-week'), value: 'this-week' });
    selections.push({ name: this.translate.instant('filter.filter-by-date-last-week'), value: 'last-week' });
    selections.push({ name: this.translate.instant('filter.filter-by-date-this-month'), value: 'this-month' });

    this.fieldDateSelection = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      placeholder: '',
      required: false,
      datasource: new FixedDataSource(selections),
    });

    this.initializeFilter();
    this.subscriptions.add(this.filter.clearFilter.subscribe(this.clearFilter));
  }

  clearFilter = (): void => this.setValue('');

  onFieldChanged = (value: string): void => {
    this.selected.emit(value);
  };
}
