import {
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewEncapsulation,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Subscription } from 'rxjs';
import { FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType } from 'shared-code-lib';
import { Filter } from '../../models/filter';
import { FilterBaseComponent } from '../filter-base.component';

@Component({
  selector: 'app-filter-by-property',
  templateUrl: './filter-by-property.component.html',
  styleUrls: ['./filter-by-property.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FilterByPropertyComponent extends FilterBaseComponent implements OnInit, OnDestroy {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Input() properties: string[] = [];
  @Input() emptyValueDisplayName: string = '';
  @Input() translationPrefix: string = '';
  @Input() smallLabel: string;
  @Output() selected = new EventEmitter<string>();

  property = '';
  contains = '';

  fieldProperty: FieldDefinition;
  fieldContains: FieldDefinition;

  propertyDataSource = new FixedDataSource(new Array<IFieldOptions>());

  subscriptions = new Subscription();

  constructor(private translateService: TranslateService, private cdk: ChangeDetectorRef) {
    super();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  ngOnInit(): void {
    this.createFields();

    if (this.emptyValueDisplayName != '') {
      this.propertyDataSource.options.push({
        name: this.translateService.instant(`filter.${this.translationPrefix}.${this.emptyValueDisplayName}`),
        value: '',
      });
    }

    this.properties.forEach((cp) => {
      this.propertyDataSource.options.push({
        name: this.translateService.instant(`filter.${this.translationPrefix}.${cp.toLowerCase()}`),
        value: cp,
      });
    });

    this.initializeFilter();
    this.subscriptions.add(this.filter.clearFilter.subscribe(this.clearFilter));
  }

  clearFilter = (): void => this.setValue(':');

  setValue = (val: string): void => {
    const parts = this.getFilterParts(val);
    if (parts.length < 2) return;

    this.property = parts[0];
    this.contains = parts[1];
    this.cdk.detectChanges();

    this.setFilterValue();
  };

  onPropertyChanged = (property: string): void => {
    this.property = property;

    this.onFieldChanged();
  };

  onContainsChanged = (contains: string): void => {
    this.contains = contains;

    this.onFieldChanged();
  };

  onFieldChanged = (): void => {
    this.setFilterValue();
    this.selected.emit(this.filter.value);
  };

  setFilterValue = (): void => {
    this.filter.value = this.property ? `${this.property}:${this.contains}` : '';
  };

  createFields = (): void => {
    this.fieldProperty = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      smallLabel: this.smallLabel
        ? this.translateService.instant(`filter.${this.translationPrefix}.${this.smallLabel}`)
        : '',
      placeholder: '',
      required: false,
      datasource: this.propertyDataSource,
    });

    this.fieldContains = new FieldDefinition({
      type: MetaDataType.Text,
      label: '',
      placeholder: '',
      required: false,
    });
  };
}
