import { Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';
import { Filter } from '../../models/filter';

@Component({
  selector: 'app-filter-by-device-column',
  templateUrl: './filter-by-device-column.component.html',
  styleUrls: ['./filter-by-device-column.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FilterByDeviceColumnComponent {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  onFilterSelected = (value: string) => this.selected.emit(value);
}
