import { Component, EventEmitter, Input, Output } from '@angular/core';
import { Filter } from '../../models/filter';

@Component({
  selector: 'app-filter-by-workflow-name',
  templateUrl: './filter-by-workflow-name.component.html',
  styleUrls: ['./filter-by-workflow-name.component.scss'],
})
export class FilterByWorkflowNameComponent {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  onFilterChanged = (value: string): void => {
    this.selected.emit(value);
  };
}
