import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class FilterByInterfaxInboxPropertiesComponentLoadedService {
  private componentLoadedSubject = new Subject<void>();
  componentLoaded$ = this.componentLoadedSubject.asObservable();

  notifyComponentLoaded() {
    this.componentLoadedSubject.next();
  }
}
