import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { Params } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { Subscription } from 'rxjs';
import { FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType } from 'shared-code-lib';
import { Filter } from '../../models/filter';
import { HydraApiService } from '../../services/hydra-api.service';
import { ApiResult } from '../../services/models';
import { FilterBaseComponent } from '../filter-base.component';

@Component({
  selector: 'app-filter-by-grouping',
  templateUrl: './filter-by-grouping.component.html',
  styleUrls: ['./filter-by-grouping.component.scss'],
})
export class FilterByGroupingComponent extends FilterBaseComponent implements OnInit, OnDestroy {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  fieldGrouping: FieldDefinition;
  grouping = '';

  subscriptions = new Subscription();

  constructor(private hydraApi: HydraApiService, private translateService: TranslateService) {
    super();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  ngOnInit(): void {
    this.createFields();

    this.initializeFilter();
    this.subscriptions.add(this.filter.clearFilter.subscribe(this.clearFilter));
  }

  clearFilter = (): void => this.setValue('');

  setValue = (val: string): void => void (this.grouping = this.filter.value = val);

  onGroupingChanged = (grouping: string): void => {
    this.filter.value = grouping;
    this.selected.next(this.filter.value);
  };

  createFields = (): void => {
    this.fieldGrouping = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      smallLabel: this.translateService.instant('filter.grouping'),
      placeholder: '',
      required: false,
      datasource: new groupingsDataSource(this.hydraApi, this.translateService),
    });
  };
}

class groupingsDataSource extends FixedDataSource {
  routeParams: Params;

  constructor(private hydraApiService: HydraApiService, private translateService: TranslateService) {
    super();
  }

  successCallBack: (items: IFieldOptions[]) => void;
  failureCallBack: (err: any) => void;

  getItems(
    startsWith: string,
    currentValue: string,
    successCallBack: (items: IFieldOptions[]) => void,
    failureCallBack: (err: any) => void
  ): void {
    this.successCallBack = successCallBack;
    this.failureCallBack = failureCallBack;

    this.hydraApiService.getGroupings(this.groupingsLoaded, this.failed);
  }

  groupingsLoaded = (apiResult: ApiResult): void => {
    const options = new Array<IFieldOptions>();

    options.push({ name: this.translateService.instant('filter-by-grouping.all'), value: '' });

    apiResult.object.forEach((item) => {
      options.push({ name: item.name, value: item.id });
    });

    this.successCallBack(options);
  };

  failed = (err): void => {
    this.failureCallBack(err);
  };
}
