import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { EnumDataSource, IFieldOptions } from 'shared-code-lib';
import { Filter } from '../../models/filter';

@Component({
  selector: 'app-filter-by-browser',
  templateUrl: './filter-by-browser.component.html',
  styleUrls: ['./filter-by-browser.component.scss'],
})
export class FilterByBrowserComponent implements OnInit {
  @Input() filter: Filter;
  @Input() layoutVertically: boolean;
  @Output() selected = new EventEmitter<string>();

  options: IFieldOptions[] = [];

  constructor(private translateService: TranslateService) {}

  ngOnInit(): void {
    this.options = this.getBrowserOptions();
  }

  getBrowserOptions = () => {
    const browsersDatasource = new EnumDataSource(Browsers, 'Browsers', this.translateService);

    return [
      {
        name: this.translateService.instant('Browsers.Any'),
        value: '',
      },
      ...browsersDatasource.options,
    ];
  };

  onFilterSelected = (value: string) => this.selected.emit(value);
}

export enum Browsers {
  Unknown = '0',
  Chrome = '1',
  Firefox = '2',
  Edge = '3',
  Safari = '4',
  Opera = '5',
  PrintToFax = '6',
  HP = '7',
  KM = '8',
  MobileChrome = '9',
  MobileFirefox = '10',
  MobileEdge = '11',
  MobileSafari = '12',
  MobileOpera = '13',
}
